<template>
  <v-container>
    <v-layout
      text-xs-center
      wrap
      row
    >
      <v-flex xs12>
        <h1>Checkout</h1>
      </v-flex>

      <v-flex xs12>
        <p>Product: {{basket().product}}</p>
      </v-flex>      
      <v-flex xs12>
        <p>Price: {{getPrice(basket().amount)}}  {{basket().currency}}</p>
      </v-flex>
      <v-flex xs12>
      <PayPal
        :amount="getPrice(basket().amount)"
        currency="EUR"
        :button-style="myStyle"
        :client="credentials"
        v-on:payment-authorized="paymentAuthorized"
        v-on:payment-completed="paymentCompleted"
        env="sandbox">
      </PayPal>
      </v-flex>
      <v-flex xs12>
          <v-alert
          :value="true"
          type="success"
          v-if="payed"
        >
           {{basket().product}} wurde erfolgreich gebucht! Viel Spaß!
        </v-alert>
      </v-flex>
        <v-flex xs12 style="padding-top:15">
          
        <v-progress-circular
          indeterminate
          color="primary"
          v-if="processing"
        > Warte auf Bestätigung der Zahlung</v-progress-circular>
      </v-flex>
      
    </v-layout>
  </v-container>
</template>

<script>
import PayPal from 'vue-paypal-checkout'
  export default {
    data() {
    return {
      payed:false,
      processing:false,
      credentials: {
        sandbox: 'ATMNx4b8XKdZiQPHfgDsp-K8Xat2lYvI177O2X9ijtctLy_8XLqnpIkrVMgx0TEJM1PksbGqoAt9tFuf',
        production:'AasQHgA1Ysbxa-2RwImBerZYRX4oD5jLq7Mh8hTNCnK3Ip0Q342S07qNv14oznkSKBNBq5XfZgabLCu0'
      },
      myStyle: {
      label: 'checkout',
      size:  'responsive',
      shape: 'rect',
      color: 'white'
      }
  
    }
  },
   methods:{
     goEvents(){
       this.$router.push('events')
     },
     getPrice(price){
       return price.replace('€','')
     },
     basket(){
       return this.$store.getters['Payment/getBasket']
     },
     paymentCompleted: function(data){
        this.payed = true
        this.processing = false;        
     },
     paymentAuthorized: function(data){
       this.processing = true
     }
   },
    components: {
    PayPal
  },
  computed:{

  }
  }
</script>

