export const setAttendStatus = (state,item) =>{
    let newEvent = item;  
    let newEvents = state.events.map((event) => {
        if(event.text === newEvent.text){
           event.attend = true;
           return event;    
        }
        return event;
    });
    
    
    return state.events = newEvents
}

export const submitEvent = (state,event) => {
    let oldEvents = state.events;
    oldEvents.push(event);
    state.events = oldEvents;
}
