<template>
         <v-flex xs12 sm6 md3>
              <v-card :color="white" class="black--text">
                <v-layout row wrap>
                  <v-card-title xs12 >                  
                   <h4> {{item.text}}</h4>                  
                  </v-card-title> 
                  <v-flex xs12>
                     <v-subheader>   {{ item.date }}         </v-subheader>
                  </v-flex> 
                </v-layout>
                <v-container>
                <v-layout row>                
                  <v-flex xs6>
                    <div>
                     
                        <div>{{item.place}}</div>                  
                      
                    </div>
                  </v-flex>
                  <v-flex xs6>
                    <v-img
                      :src="item.image"
                      height="125px"
                      contain
                    ></v-img>                    
                  </v-flex>
                </v-layout>
                </v-container>
                <v-divider light></v-divider>
                <v-card-actions class="pa-3">
                  <div v-if="!item.attend">
                     <v-btn depressed v-if="containsEuro(item.price)"  @click="buyTicket(item)">Ticket kaufen</v-btn>
                     <v-btn depressed v-if="!containsEuro(item.price)"  @click="attendEvent(item)">Teilnehmen</v-btn>
                  </div>
                  <v-btn v-else class="success" disabled="true">Angemeldet</v-btn>
            
                  <v-spacer></v-spacer>
                  <p>{{item.price}}</p>          
                </v-card-actions>
                <v-snackbar
                  v-model="attend"
                   :top="true">
                Du nimmst am Event "{{item.text}}" teil.
                <v-btn
                    color="pink"
                    flat
                    @click="attend = false"
                >
                    Close
                        </v-btn>
                </v-snackbar>
              </v-card>
            </v-flex>
</template>

<script>

export default {
 data(){
     return {
         attend:false,
         colors:{
           1:"purple",
           2:"cyan darken-2",
           3:"blue-grey darken-2"
         }
     }
 },
 props:['item'],
 methods:{
    containsEuro(price){
      if(price != null && price.includes("€"))
        return true;
      else 
        return false;
        
    },
    cutAfter15(text){
        return text.substring(0,15)
    },
    buyTicket(item){
       this.$store.dispatch('Payment/buyTicket', item)
       .then(() => {
          this.$router.push('/checkout')          
       });       
    },
    attendEvent(item){
        this.attend = true;
        this.$store.dispatch('Events/setAttendStatus', item);
         
    },
    color(){
      var random = 3 - Math.random() * (3 - 1); 
      
      return this.colors[Math.round(random)]
    }
  }
}
</script>

