
export const setProfile= (state, profile) => {
    state.profile = profile
}
export const  setAccessToken= (state, accessToken) => {
    state.accessToken = accessToken
  }
  export const  setIdToken= (state, idToken) => {
    state.idToken = idToken
  }
  export const  setVerification= (state, verification) => {
    state.verification = verification
  }
  export const setAuthenticated= (state, isAuthenticated) => {
    state.isAuthenticated = isAuthenticated
}
