<template>
<v-container fluid style="padding-left:0;padding-right:0;padding-top:0">
     <v-layout row wrap >  
       <v-flex 12xs>           
          <v-card>
            <v-container
              fluid
              grid-list-lg  
              style="background:#EBEBEB"            
            >  
                 
           <v-layout row wrap>
             
              <EventItem v-if="loaded" v-for="(item,i) in items"  
                :key="i" :item="item" />
           </v-layout>
            </v-container>
          </v-card>      
       </v-flex>
     </v-layout>
 </v-container>
</template>

<script>
import EventItem from './EventItem.vue'
export default { 
  data(){
    return {
      loaded:false
    }
  } ,
  computed:{
      items(){          
          let events = this.$store.getters['Events/getEvents'];
          this.loaded = true;
          return events

      }
  },
  components:{
    EventItem
  }
}
</script>

