<template>
<v-container>
  <v-card>
      <v-container
          fluid
          fill-height
          grid-list-lg
        >       
        <p v-if="lade">ss</p>
            <v-flex xs12>
              <v-card color="blue-grey darken-2" class="white--text">
                <v-card-title primary-title>
                  <div class="headline">Event erstellen</div>
                  <v-flex xs12>
                  <div>Erstelle ein Event</div>
                  </v-flex>
                </v-card-title>
                <v-card-actions>
                  <v-layout row wrap> 
                    <v-flex xs12 md6>
                      <v-text-field
                          label="solo"
                          placeholder="Gib einen Titel ein"
                          solo
                          v-model="event.text"
                        ></v-text-field>
                    </v-flex>
                    <v-flex v-if="role === 'FARANTO_ADMIN'" xs12 md6>
                      <v-checkbox v-model="checked" color="white" label="Kostenpflichtig" >
                        <div slot="label">
                          <strong class="white--text">Kostenpflichtig</strong>
                        </div>
                      </v-checkbox>
                    </v-flex>
                    <v-flex xs12>  
                   
                      <v-text-field
                          v-model="event.price"
                          v-if="checked"
                          label="solo"
                          placeholder="Gib einen Preis ein"
                          solo
                        ></v-text-field>
                                 
                  <v-date-picker full-width v-model="event.date" :landscape="landscape" :reactive="reactive"></v-date-picker>
                  </v-flex>
                  <v-flex>
                    <v-btn depressed @click="submitEvent()">Event erstellen</v-btn>
                  </v-flex>
                  </v-layout>
                </v-card-actions>
              </v-card>
            </v-flex>        
      
      </v-container>
    </v-card>
<v-snackbar
                  v-model="attend"
                   :top="true">
                Du hast das Event "{{event.text}}" erstellt.
                <v-btn
                    color="pink"
                    flat
                    @click="attend = false"
                >
                    Close
                        </v-btn>
                </v-snackbar>
      
</v-container>      
</template>

<script>
import {Auth} from 'aws-amplify'
import jwt from 'jsonwebtoken'
  export default {
  data() {
    return {
      event:{
        text:null,
        price:null,
        date:null
      },
      profile:null,
      picker: new Date().toISOString().substr(0, 10),
      landscape: false,
      reactive: false,
      checked:false,
      lade:false,
      attend:false
    }
  },
  methods:{
    submitEvent(){
      if(this.event.price != null){
        this.event.price += '€'
      }       
      this.$store.dispatch('Events/submitEvent',this.event)
      this.attend = true;
    },
    getRole(){ 
      let that = this;
      let ret = this.$store.getters['Auth/accessToken'].then(x => that.role = x)    
      
    }
  },
  computed:{
      role(){              
      let ret = this.$store.getters['Auth/accessToken']      
      console.log(ret)
      return ret; 

      } 
  },
  mounted(){        
    Auth.currentUserPoolUser().then((userinfo)=>{
      let id = Object.entries(userinfo.storage)[6][1];  
      let group = jwt.decode(id)['cognito:groups'][0]
      this.$store.dispatch('Auth/setUser',group)
    })
    
  }
}
</script>

