<template>
  <div>
    <h1>Authenticating...</h1>
  </div>
</template>

<script>
export default {
  mounted () {
    var accessToken = this.$route.hash.match(/(?:access_token)=([^&]+)/)
    var idToken = this.$route.hash.match(/(?:id_token)=([^&]+)/)
    //var verification = this.$route.hash.match(/(?:state)=([^&]+)/)
    var expiresIn = this.$route.hash.match(/(?:expires_in)=(\d+)/)
   
    if (!accessToken) {
      this.$router.push({ name: 'error', params: { message: 'The authentication response did not contain a valid access_token' } })
      return
    }
    if (!idToken) {
      this.$router.push({ name: 'error', params: { message: 'The authentication response did not contain a valid id_token' } })
      return
    }
    /*
    if (!verification) {
      this.$router.push({ name: 'error', params: { message: 'The authentication response did not contain a valid verification state value' } })
      return
    }
    */
    if (!expiresIn) {
      this.$router.push({ name: 'error', params: { message: 'The authentication response did not contain a valid token expiry date' } })
      return
    }
    this.$store.dispatch('Auth/authenticate', {
      accessToken: accessToken[1],
      idToken: idToken[1],
      verification: 'dsfer',//verification[1],
      expiresIn: expiresIn[1]
    })
  }
}
</script>