import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Events from './components/EventsView.vue'
import Payment from './components/Payment.vue'
import Checkout from './components/Checkout.vue'
import CommunityDashboard from './components/Community/CommunityDashboard.vue'
import CreateEvent from './components/Community/CreateEvent.vue'
import Login from './components/Auth/Login.vue'
import error from './components/Error.vue'
import Callback from './components/Callback.vue'

import store from './store'

Vue.use(Router)

var router =  new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        requiresAuth:false
      }
    },
    {
      path: '/callback',
      name: 'callback',
      component: Callback,
      meta:{
        requiresAuth:false
      }
    },
    {
      path: '/',
      name: 'home',
      component: Home,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splismileytting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      path: '/events',
      name: 'events',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: Events,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/payment',
      name: 'payment',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: Payment,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/checkout',
      name: 'checkout',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: Checkout,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/error',
      name: 'error',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: error,
      meta:{
        requiresAuth:false
      }
    }
    ,{
      path: '/community',
      name: 'community',
      component: CommunityDashboard,
      meta:{
        requiresAuth:true
      }
    },{
      path: '/community/create',
      name: 'community-create',
      component: CreateEvent,
      meta:{
        requiresAuth:true
      }
    }
  ]
})


router.beforeEach((to, from, next) => {
  // Use the page's router title to name the page
  if (to.meta && to.meta.title) {
    document.title = to.meta.title
  }


  // Redirect to the login page if not authenticated
  // for pages that have 'auth: true' set
  if (to.meta && to.meta.requiresAuth !== undefined) {
    if (to.meta.requiresAuth) {
      if (store.getters['Auth/isAuthenticated']) {
        next()
      } else {
        router.push({ name: 'login' })
      }
    } else {
      if (store.getters.isAuthenticated) {        
        router.push({ name: 'events' })
      } else {
        next()
      }
    }
  } else {
    router.push({ name: 'error' ,params: { message: 'Router failed'+to.fullPath } })
  }
})

export default router;